import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { MenuItem } from 'primeng/components/common/menuitem';
import * as _ from 'lodash';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { BusinessCenterManageHttp } from '../../http/business-center-manage.http.service';
import { addFieldManageBean } from '../bean/field-manage-list.bean';

@Component({
    selector: 'field-manage-list-update',
    templateUrl: './field-manage-list-update.component.html',
    styleUrls: ['./field-manage-list-update.component.scss'],
})

export class FieldManageListUpdateComponent implements OnInit {
    dtEntity: any;
    @Input() inValue: any;
    @Input() inValue1: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter<any>();
    mesg: any;//报文头信息
    msgs: Message[] = [];//信息提示框
    items: MenuItem[];//下一步
    activeIndex: number;
    vlaue: any;//显示哪个div
    tableList2: any = [];
    daField: string;
    addFieldManageBean: addFieldManageBean = new addFieldManageBean();
    //校验
    userform: FormGroup;
    code: any;
    FiledType: any = [];
    FiledView: any = [];
    englishField: string;
    isShowTip: boolean = true;
    daName: string;
    kk: string = '日期型';
    entityName: string;
    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public confirmationService: ConfirmationService,
        public httpService: BusinessCenterManageHttp,
        public fb: FormBuilder,
    ) {
        this.userform = this.fb.group({
            "daField": new FormControl('', [Validators.required, Validators.pattern(/^[\w]+$/)]),
            "daName": new FormControl(''),
            // , [Validators.required,,Validators.pattern(/^[\u4e00-\u9fa5]+$/)]
            "filedType": new FormControl('', Validators.required),
            "describe": new FormControl(''),
            "filedLen": new FormControl('', Validators.required),
            "filedView": new FormControl('', Validators.required),
            'entityName': new FormControl('', Validators.required),
        });
        this.codeValues();
        this.FiledType = this.code['FiledType'];//字段类型
        this.FiledView = this.code['FiledView'];//界面展示形式
        this.dtEntity = this.code['dtEntity'];//实体
        this.tableList2 = [{
            fieldValue: '',
            describe: ''
        }];
    }

    ngOnInit() {
        this.addFieldManageBean.filedType = this.FiledType[0].value;
        this.addFieldManageBean.filedView = this.FiledView[0].value;
        this.daField = this.inValue;
        this.entityName = this.inValue1;
        this.activeIndex = 0;
        this.vlaue = '0';
        this.items = [
            {
                label: '字段基本信息配置',
                command: (event: any) => {
                    this.activeIndex = 0;
                }
            },
            {
                label: '字段取值配置',
                command: (event: any) => {
                    this.activeIndex = 1;
                }
            },
        ];
        this.echoDetail();
    }
    change() {
        if (this.addFieldManageBean.filedType == '03') {
            this.addFieldManageBean.filedView = '03';
        } else {
            this.addFieldManageBean.filedView = this.FiledView[0].value;
        }
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //下一步
    next() {
        if (this.activeIndex == 0) {
            this.isShowTip = true;
            //校验的
            for (const i in this.userform.controls) {
                this.userform.controls[i].markAsDirty();
            }
            //处理是否提交
            for (const i in this.userform.controls) {
                if (!this.userform.controls[i].valid) {
                    return;
                }
            }
            if (this.addFieldManageBean.filedType == '03') {
                this.addFieldManageBean.filedView = '03';
            }
            this.addFieldManageBean.filedStatus = '0';
            this.addFieldManageBean.daField = this.daField;
            this.addFieldManageBean.daName = this.daName;
            this.httpService.createOrStoreAttrItemOfPms(this.addFieldManageBean).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.echoDetail();
                    this.activeIndex = this.activeIndex + 1;
                    this.vlaue = this.activeIndex;
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            })
        } else if (this.activeIndex == 1) {
        }
    }
    //上一步
    prev() {
        this.activeIndex = this.activeIndex - 1;
        this.vlaue = this.activeIndex;
    }
    //删除
    delete(i) {
        this.tableList2.splice(i, 1);
        this.tableList2 = _.clone(this.tableList2);
    }
    //修改
    add() {
        let aa = _.clone(this.tableList2);
        aa.push({
            fieldValue: '', describe: '',
        });
        this.tableList2 = aa;
    }

    //保存
    saveClick() {
        if (this.addFieldManageBean.filedView == '02') {//下拉列表框
            let param = {
                daField: this.daField,
                itemVals: this.tableList2,
                entityName: this.addFieldManageBean.entityName
            }
            this.httpService.createOrStorAttrItemValOfPms(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.outValue.emit(false);
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            })
        } else {
            this.isShowTip = true;
            //校验的
            for (const i in this.userform.controls) {
                this.userform.controls[i].markAsDirty();
            }
            //处理是否提交
            for (const i in this.userform.controls) {
                if (!this.userform.controls[i].valid) {
                    return;
                }
            }
            if (this.addFieldManageBean.filedType == '03') {
                this.addFieldManageBean.filedView = '03';
            }
            this.addFieldManageBean.filedStatus = '0';
            this.addFieldManageBean.daField = this.daField;
            this.addFieldManageBean.daName = this.daName;
            this.httpService.createOrStoreAttrItemOfPms(this.addFieldManageBean).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.isShowTip = false;
                    this.outValue.emit(false);
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            })
        }
    }

    //先调详情
    echoDetail() {
        let param = {
            daField: this.daField,
            entityName: this.entityName
        }
        this.httpService.queryAttrItemInfoOfPms(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.addFieldManageBean = data;
                this.daName = data.daName;
                if (data.fieldList) {
                    this.tableList2 = data.fieldList;
                } else {
                    this.tableList2 = [{
                        fieldValue: '',
                        describe: ''
                    }];
                }
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    rest() {
        if (this.activeIndex == 0) {
            this.isShowTip = false;
            this.addFieldManageBean = new addFieldManageBean();
            this.addFieldManageBean.filedType = this.FiledType[0].value;
            this.addFieldManageBean.filedView = this.FiledView[0].value;
        } else if (this.activeIndex == 1) {
            this.tableList2.forEach(element => {
                element.fieldValue = '';
                element.describe = '';
            });
        }
    }
    cancel() {
        this.outValue.emit(false);
    }
}