import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { Message } from 'primeng/primeng';
import { MenuItem } from 'primeng/components/common/menuitem';
import { BusinessCenterManageHttp } from '../../http/business-center-manage.http.service';

@Component({
    selector: 'field-manage-list-detail',
    templateUrl: './field-manage-list-detail.component.html',
    styleUrls: ['./field-manage-list-detail.component.scss'],
})
// 字段详情

export class FieldManageListDeatilComponent implements OnInit {
    @Input() inValue: any;
    @Input() inValue1: any;
    mesg: any;//报文头信息
    msgs: Message[] = [];//信息提示框
    items: MenuItem[];//下一步
    activeIndex: number;
    vlaue: any;//显示哪个div
    tableList2: any = [];
    daField: string;
    detail:any={};
    code:any;
    FiledType:any=[];
    FiledView:any;
    entityName: string;//
    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public httpService: BusinessCenterManageHttp
    ) {
        this.codeValues();
        this.FiledType = this.code['FiledType'];//字段类型
        this.FiledView = this.code['FiledView'];//界面展示形式
    }

    ngOnInit() {
        this.daField = this.inValue;
        this.entityName = this.inValue1;
        this.echoClick();
    }
    //详情回显
    echoClick() {
        let param = {
            daField: this.daField,
            entityName: this.entityName
        }
        this.httpService.queryAttrItemInfoOfPms(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.detail=data;
                this.tableList2=data.fieldList
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
}