import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { FiledManageListBean } from './bean/field-manage-list.bean';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { BusinessCenterManageHttp } from '../http/business-center-manage.http.service';

@Component({
    selector: 'field-manage-list',
    templateUrl: './field-manage-list.component.html',
    styleUrls: ['./field-manage-list.component.scss'],
})

export class FieldManageListComponent implements OnInit {
    dtEntity: any;
    addDisplay: boolean = false;
    mesg: any;//报文头信息
    msgs: Message[] = [];//信息提示框
    showFlag: string;
    headerTitle: string;
    filedManageListBean: FiledManageListBean = new FiledManageListBean();
    list: any = [];
    total: number;
    first: number = 0;//首页
    code: any;
    FiledType: any = [];//字段类型
    FiledView: any = [];//界面展示形式
    FiledStatus: any = [];//界面展示形式
    inValue1: any;
    inValue2: any;
    inValue3: any;
    constructor(
        public confirmationService: ConfirmationService,
        public httpService: BusinessCenterManageHttp,
        public commfunc: Commfunc
    ) {
        this.codeValues();
        this.FiledView = this.code['FiledView'];//界面展示形式
        this.FiledStatus = this.code['FiledStatus'];//状态
        this.FiledType = this.code['FiledType'];//字段类型
        this.dtEntity=this.code['dtEntity'];//实体
    }

    ngOnInit() {
        this.filedManageListBean.pageNum = 1;
        this.filedManageListBean.pageSize = 10;
        this.query()
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //查询
    queryClick() {
        this.filedManageListBean.pageNum = 1;
        this.filedManageListBean.pageSize = 10;
        this.query()
    }
    query() {
        this.filedManageListBean['doFlag'] = '1';
        this.httpService.queryAttrItemListOfPms(this.filedManageListBean).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.total = data.total;
                this.list = data.fieldsList ? data.fieldsList : [];
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //重置
    reset() {
        this.filedManageListBean = new FiledManageListBean();
    }

    //分页
    paginate(event) {
        //每页显示的条数
        this.filedManageListBean.pageSize = Number(event.rows);
        //当前页
        let currentPage = event.page + 1;
        //console.log(currentPage)
        this.filedManageListBean.pageNum = currentPage;
        this.first = (currentPage - 1) * this.filedManageListBean.pageSize;
        this.query();//调用查询的方法
    }

    //新增
    add() {
        this.addDisplay = true;
        this.showFlag = '1';
        this.headerTitle = '字段列表新增';
    }
    //新增回调
    outValueAdd(param) {
        if (param['flag']) {
            this.query();
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '提示', detail: "新增成功！" });
        }
        this.addDisplay = param.display;

    }
    //修改
    update(item) {
        this.addDisplay = true;
        this.showFlag = '2';
        this.headerTitle = '字段列表修改';
        this.inValue2 = item.daField;
        this.inValue3 = item.entityName;
    }

    //修改回调
    outValueUpdate(event) {
        this.addDisplay = event;
        this.query();
    }

    //详情
    check(item) {
        this.addDisplay = true;
        this.showFlag = '3';
        this.headerTitle = '字段列表详情';
        this.inValue1 = item.daField;
        this.inValue3 = item.entityName;
    }

    //启用
    toNewUse(item) {
        this.func(item, '0');
    }
    //停用
    delete(item) {
        this.confirmationService.confirm({
            message: '确定要停用' + item.daField + '吗?',
            accept: () => {
                this.func(item, '1');
            }
        });
    }
    func(item, filedStatus) {
        let param = {
            daField: item.daField,
            filedStatus,
            entityName:item.entityName
        }
        this.httpService.createOrStoreAttrItemOfPms(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.queryClick();
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }

}