import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';
import { MenuItem } from 'primeng/components/common/menuitem';
import * as _ from 'lodash';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { addFieldManageBean } from '../bean/field-manage-list.bean';
import { BusinessCenterManageHttp } from '../../http/business-center-manage.http.service';

@Component({
    selector: 'field-manage-list-add',
    templateUrl: './field-manage-list-add.component.html',
    styleUrls: ['./field-manage-list-add.component.scss'],
})

export class FieldManageListAddComponent implements OnInit {
    dtEntity: any;
    @Output() outValue: EventEmitter<any> = new EventEmitter<any>();
    mesg: any;//报文头信息
    addFieldManageBean: addFieldManageBean = new addFieldManageBean();//规则信息
    msgs: Message[] = [];//信息提示框
    items: MenuItem[];//下一步
    activeIndex: number;
    vlaue: any;//显示哪个div
    tableList2: any = [];
    //校验
    userform: FormGroup;
    code: any;
    FiledType: any = [];
    FiledView: any = [];
    englishField: string;//英文字段名称
    isShowTip: boolean = true;
    list:any=[];
    kk:string='日期型';
    constructor(
        public commfunc: Commfunc,
        public router: Router,
        public confirmationService: ConfirmationService,
        public fb: FormBuilder,
        public httpService: BusinessCenterManageHttp
    ) {
        this.userform = this.fb.group({
            "daField": new FormControl('', [Validators.required,Validators.pattern(/^[\w]+$/)]),
            "daName": new FormControl('', [Validators.required,,Validators.pattern(/^[\u4e00-\u9fa5]+$/)]),
            "filedType": new FormControl('', Validators.required),
            "describe": new FormControl(''),
            "filedLen": new FormControl('', Validators.required),
            "filedView": new FormControl('', Validators.required),
            'entityName':new FormControl('', Validators.required),
        });
        this.codeValues();
        this.FiledType = this.code['FiledType'];//字段类型
        this.FiledView = this.code['FiledView'];//界面展示形式
        this.dtEntity=this.code['dtEntity'];//实体
        //console.log(this.FiledView)
        this.tableList2= [{
            fieldValue:'',
            describe:''
        }];

    }

    ngOnInit() {

        this.addFieldManageBean.filedType = this.FiledType[0].value;
        this.addFieldManageBean.filedView = this.FiledView[0].value;
        this.activeIndex = 0;
        this.vlaue = '0';
        this.items = [
            {
                label: '字段基本信息配置',
                command: (event: any) => {
                    this.activeIndex = 0;
                }
            },
            {
                label: '字段取值配置',
                command: (event: any) => {
                    this.activeIndex = 1;
                }
            },
        ];

    }
    change () {
        if (this.addFieldManageBean.filedType=='03') {
            this.addFieldManageBean.filedView='03';
        }else{
            this.addFieldManageBean.filedView = this.FiledView[0].value;
        }
    }
    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret //code是自己声明的变量
    }
    //下一步
    nextclick() {
        if (this.activeIndex == 0) {
            if (this.addFieldManageBean.filedType=='03') {
                this.addFieldManageBean.filedView='03';
            }
            this.addFieldManageBean.filedStatus = '0';
            this.httpService.createOrStoreAttrItemOfPms(this.addFieldManageBean).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.isShowTip = false;
                    this.activeIndex = this.activeIndex + 1;
                    this.vlaue = this.activeIndex;
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            })
        } else if (this.activeIndex == 1) {
        }
    }
    //下一步
    next () {
        this.isShowTip = true;
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        let param ={
            daField:this.addFieldManageBean.daField
        }
        this.httpService.queryAttrItemListOfPms(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.list = data.fieldsList;
                if (data.fieldsList.length!=0) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: '字段英文名已存在' });
                    return;
                }else{
                    this.nextclick();             
                }
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
            } else {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
            }
        }, error => {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
        })
    }
    //上一步
    prev() {
        this.activeIndex = this.activeIndex - 1;
        this.vlaue = this.activeIndex;
    }
    //删除
    delete(i) {
        this.tableList2.splice(i, 1);
        this.tableList2 = _.clone(this.tableList2);
    }
    //新增
    add() {
        let aa = _.clone(this.tableList2);
        aa.push({
            fieldValue: '', describe: '',
        });
        this.tableList2 = aa;
    }
    cancel() {
        this.outValue.emit({display:false});
    }
    //保存查询
    queryClick() {
        if (this.addFieldManageBean.filedView == '02') {
            let param = {
                daField: this.addFieldManageBean.daField,
                itemVals: this.tableList2,
                entityName:this.addFieldManageBean.entityName
            }
            this.httpService.createOrStorAttrItemValOfPms(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.outValue.emit({display:false,flag:true});
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            })
        } else {
            if (this.addFieldManageBean.filedType=='03') {
                this.addFieldManageBean.filedView='03';
            }
            this.addFieldManageBean.filedStatus = '0';
            this.httpService.createOrStoreAttrItemOfPms(this.addFieldManageBean).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.isShowTip = false;
                    this.outValue.emit({display:false,flag:true});
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            })
        }
    }

    //保存
    saveClick() {
        this.isShowTip = true;
        //校验的
        for (const i in this.userform.controls) {
            this.userform.controls[i].markAsDirty();
        }
        //处理是否提交
        for (const i in this.userform.controls) {
            if (!this.userform.controls[i].valid) {
                return;
            }
        }
        if (this.addFieldManageBean.filedView != '02') {
            let param ={
                daField:this.addFieldManageBean.daField,
            }
            this.httpService.queryAttrItemListOfPms(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.list = data.fieldsList;
                    if (data.fieldsList.length!=0) {
                        this.msgs = [];
                        this.msgs.push({ severity: 'error', summary: '提示', detail: '字段英文名已存在' });
                        return;
                    }else{
                        this.queryClick();             
                    }
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
                } else {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
                }
            }, error => {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
            })
        }else{
            this.queryClick();  
        }
    }
    rest() {
        if (this.activeIndex == 0) {
            this.isShowTip = false;
            this.addFieldManageBean = new addFieldManageBean();
            this.addFieldManageBean.filedStatus = '0';
            this.addFieldManageBean.filedType = this.FiledType[0].value;
            this.addFieldManageBean.filedView = this.FiledView[0].value;
        } else if (this.activeIndex == 1) {
            this.tableList2.forEach(element => {
                element.fieldValue = '';
                element.describe = '';
            });
        }
    }
}